<template>
    <div class="idcard">
        <div class="pos" @click="openEvent('positive')">
            <div class="preview">
                <img v-if="img.positive" :src="img.positive" alt>
                <div v-else>
                    <img
                        src="https://money-arrive-static.oss-cn-shanghai-finance-1-pub.aliyuncs.com/20181203/cam.png"
                        alt
                    >
                    <div class="default">请拍摄身份证正面</div>
                </div>
            </div>
            <input
                type="file"
                ref="positive"
                accept="image/*"
                mutiple="mutiple"
                capture="camera"
                @change="e => upload(e, 'positive')"
            >
        </div>
        <div class="pos" @click="openEvent('reverse')">
            <div class="preview">
                <img v-if="img.reverse" :src="img.reverse" alt>
                <div v-else>
                    <img
                        src="https://money-arrive-static.oss-cn-shanghai-finance-1-pub.aliyuncs.com/20181203/cam.png"
                        alt
                    >
                    <div class="default">请拍摄身份证反面</div>
                </div>
            </div>
            <input
                type="file"
                ref="reverse"
                accept="image/*"
                capture="camera"
                @change="e => upload(e, 'reverse')"
            >
        </div>
        <div class="pos" @click="openEvent('face')">
            <div class="preview">
                <img v-if="img.face" :src="img.face" alt>
                <div v-else>
                    <img
                        src="https://money-arrive-static.oss-cn-shanghai-finance-1-pub.aliyuncs.com/20181203/cam.png"
                        alt
                    >
                    <div class="default">请拍摄本人脸部</div>
                </div>
            </div>
            <input
                type="file"
                ref="face"
                accept="image/*"
                capture="camera"
                @change="e => upload(e, 'face')"
            >
        </div>
        <cube-button
            class="code-btn"
            :style="[appConfig.mainBackground]"
            @click="subIdCard"
            :primary="true"
        >提交认证</cube-button>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    data() {
        return {
            img: {
                positive: '', // 正面
                reverse: '', // 反面
                face: ''
            }
        }
    },
    computed: {
        ...mapState(['certification', 'user', 'appConfig'])
    },
    beforeCreate() {
        document.title = '身份证认证'
    },
    methods: {
        subIdCard() {
            if (Object.values(this.img).some(x => x === '')) {
                this.$store.commit('setMsg', '请填写完整')
                return
            }
            this.$store.commit('loading', true)
            setTimeout(() => {
                let certification = this.user.certification
                certification.img = true
                this.$store.commit('setUser', { certification })
                this.$store.commit('setMsg', '认证成功')
                this.$store.commit('loading', false)
                this.$router.go(-1)
            }, 2000)
        },
        upload(e, input) {
            console.log(e.target.files)
            this.img[input] = URL.createObjectURL(e.target.files[0])
            console.log(URL.createObjectURL(e.target.files[0]))
        },
        openEvent(cate) {
            this.$refs[cate].dispatchEvent(new MouseEvent('click'))
        }
    }
}
</script>

