.idcard {
    .pos {
        border: 1px solid #ddd;
        box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.15);
        border-radius: 4px;
        color: #999;
    }
    .pos {
        height: 150px;
        width: 345px;
        margin: 20px auto;
        .preview {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            background: #fff;
            text-align: center;
            img {
                max-height: 100%;
                max-width: 100%;
            }
        }
        .default {
            margin-top: 10px;
        }
    }
    input[type='file'] {
        opacity: 0;
    }
    button {
        padding: 0;
        height: 40px;
        width: 340px;
        margin: 50px auto;
        line-height: 40px;
        border-radius: 4px;
        letter-spacing: 6px;
    }
}
</style>
